import {Component, OnInit} from '@angular/core';
//import {globals} from '../../globals/global.constats';
import {APIService} from '../../globals/services/API.service';
import {hunkDate} from '../../globals/pipes/hunkDate.pipe';
import {MyInnerHtmlComponent} from '../../components/my-inner-html/my-inner-html.component';
@Component({
    selector: 'hireink',
    moduleId:module.id,
    templateUrl: 'hireink.html',
    providers: [APIService],
    styleUrls: [""],
    directives: [MyInnerHtmlComponent],
    pipes:[hunkDate]
})
export class HireinkComponent implements OnInit {

    news:any=[];
    constructor(private _APIService:APIService) {

    }

    ngOnInit() {
        this.loadNews();
    }

    loadNews() {
        this._APIService.getResponseGET('api/news/where=id>0&orderBy=datum&asc=false', '').subscribe(
                data=> {
                    console.log(data);
                    this.news=data;

            },
                error=> {
                console.log(error);
            })

    }
}
